/**
 * wordpress dependencies
 */
import { head } from 'lodash';
import { __ } from '@wordpress/i18n';
import { plus } from '@wordpress/icons';
import { useEffect } from '@wordpress/element';
import { dispatch, select } from '@wordpress/data';
import { Placeholder, Button } from '@wordpress/components';
import { InnerBlocks, useBlockProps } from '@wordpress/block-editor';

/**
 * internal import
 */
import Inspector from './inspector';
import classNames from 'classnames';
import { getStyles } from './get-styles';
import { getParentBlock } from '../../utils/get-parent-block';
import { getBlockTypeIds } from '../../utils/get-block-type-ids';

function Edit( props ) {
	const { attributes } = props;
	const blockProps = useBlockProps( {
		className: classNames( 'gutenberghub-tab-buttons-container' ),
		'data-trigger': attributes.trigger,
		style: getStyles( props.attributes ),
	} );
	const template = [ [ 'ghub/tab-button', {}, [] ] ];

	const tabBlock = getParentBlock( props.clientId, 'ghub/tabs-container' );

	const blockTypeIds = getBlockTypeIds(
		tabBlock?.clientId,
		'ghub/tab-buttons-container'
	);

	const shouldFallback = head( blockTypeIds ) !== props.clientId;
	const isPatternModal = document.querySelector(
		'.block-library-query-pattern__selection-modal'
	);

	useEffect( () => {
		props.setAttributes( { shouldFallback } );
	}, [ shouldFallback ] );

	if ( shouldFallback && ! isPatternModal ) {
		return (
			<div { ...blockProps }>
				<Placeholder
					label={ __( 'Oops, Something went wrong.', 'ghub-tab' ) }
					instructions={ __(
						'You cannot take tab buttons twice.',
						'ghub-tab'
					) }
				/>
			</div>
		);
	}
	const insertTab = () => {
		const tabTemplate = [ [ 'ghub/tab-button', {}, [] ] ];
		const contentTemplate = [ [ 'ghub/tab-content', {}, [] ] ];

		const { insertBlocks } = dispatch( 'core/block-editor' );

		const contentBlockIds = getBlockTypeIds(
			tabBlock.clientId,
			'ghub/tab-contents-container'
		);

		const contentBlockId = head( contentBlockIds );
		const contentBlock =
			select( 'core/block-editor' ).getBlock( contentBlockId );

		const insertionIndex =
			select( 'core/block-editor' ).getBlock( props.clientId ).innerBlocks
				.length + 1;

		const tabCreatedBlock =
			wp.blocks.createBlocksFromInnerBlocksTemplate( tabTemplate );
		const contentCreatedBlock =
			wp.blocks.createBlocksFromInnerBlocksTemplate( contentTemplate );

		insertBlocks( tabCreatedBlock, insertionIndex, props.clientId, true );
		insertBlocks(
			contentCreatedBlock,
			contentBlock.innerBlocks.length + 1,
			contentBlockId,
			true
		);
	};

	const CustomAppender = () => (
		<Button
			onClick={ insertTab }
			icon={ plus }
			variant="secondary"
			className="gutenberghub-tabs-appender"
		/>
	);
	return (
		<>
			<div { ...blockProps }>
				<InnerBlocks
					template={ template }
					allowedBlocks={ [ 'ghub/tab-button' ] }
					renderAppender={ CustomAppender }
				/>
			</div>
			<Inspector { ...props } />
		</>
	);
}
export default Edit;
