/**
 * WordPress Dependencies
 */
import React from 'react';
import { __ } from '@wordpress/i18n';
import { PanelBody, Tip } from '@wordpress/components';
import { InspectorControls } from '@wordpress/block-editor';

/**
 * Custom Dependencies
 */
import {
	BorderControl,
	ColorSetting,
	ColorSettingWithGradient,
	CustomToggleGroupControl,
	DimensionControl,
} from '../../components';

function Inspector( props ) {
	const triggerOptions = [
		{
			label: __( 'Click', 'gutenberghub-tabs' ),
			value: 'click',
		},
		{
			label: __( 'Hover', 'gutenberghub-tabs' ),
			value: 'hover',
		},
	];
	return (
		<>
			<InspectorControls>
				<PanelBody title={ __( 'General', 'gutenberghub-tabs' ) }>
					<CustomToggleGroupControl
						isBlock
						attributeKey="trigger"
						options={ triggerOptions }
						label={ __( 'Tabs Trigger', 'gutenberghub-tabs' ) }
					/>
					<Tip>
						{ __(
							'Trigger will works only on frontend',
							'gutenberghub-tabs'
						) }
					</Tip>
				</PanelBody>
			</InspectorControls>
			<InspectorControls group="color">
				<ColorSetting
					attrKey={ 'tabsTextColor' }
					label={ __( 'Button Text Color', 'gutenberghub-tabs' ) }
				/>

				<ColorSettingWithGradient
					attrBackgroundKey={ 'tabsBackgroundColor' }
					attrGradientKey={ 'tabsGradientBackground' }
					label={ __( 'Button Background', 'gutenberghub-tabs' ) }
				/>

				<ColorSetting
					attrKey={ 'activeTabTextColor' }
					label={ __(
						'Active Button Text Color',
						'gutenberghub-tabs'
					) }
				/>

				<ColorSettingWithGradient
					attrBackgroundKey={ 'activeTabBackgroundColor' }
					attrGradientKey={ 'activeTabGradientBackground' }
					label={ __(
						'Active Button Background',
						'gutenberghub-tabs'
					) }
				/>
				<ColorSetting
					attrKey={ 'hoverTabTextColor' }
					label={ __(
						'Hover Button Text Color',
						'gutenberghub-tabs'
					) }
				/>
				<ColorSettingWithGradient
					attrBackgroundKey={ 'hoverTabBackgroundColor' }
					attrGradientKey={ 'hoverTabGradientBackground' }
					label={ __(
						'Hover Button Background',
						'gutenberghub-tabs'
					) }
				/>
			</InspectorControls>
			<InspectorControls group="dimensions">
				<DimensionControl
					showDefaultPadding
					paddingAttributeKey="tabsPadding"
					paddingLabel={ __(
						'Tab Buttons Padding',
						'gutenberghub-tabs'
					) }
					label={ __( 'Tabs Padding', 'gutenberghub-tabs' ) }
				/>
				<DimensionControl
					showDefaultPadding
					paddingLabel={ __(
						'Active Tab Button Padding',
						'gutenberghub-tabs'
					) }
					paddingAttributeKey="activeTabPadding"
				/>
			</InspectorControls>
			<InspectorControls group="border">
				<BorderControl
					showDefaultBorder
					showDefaultBorderRadius
					attrBorderKey="tabsBorder"
					attrBorderRadiusKey="tabsBorderRadius"
					borderLabel={ __(
						'Tab Buttons Border',
						'gutenberghub-tabs'
					) }
					borderRadiusLabel={ __(
						'Tab Buttons Border Radius',
						'gutenberghub-tabs'
					) }
				/>
				<BorderControl
					showDefaultBorder
					isShowBorderRadius={ false }
					attrBorderKey="activeTabBorder"
					borderLabel={ __(
						'Active Tab Button Border',
						'gutenberghub-tabs'
					) }
				/>
			</InspectorControls>
		</>
	);
}

export default Inspector;
